import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Dish from './Dish'
import ConsumeType from './ConsumeType'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      disableNew: true,
      disableEdit: true,
    },
  },
  childDishId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Dish.getBuffet,
        itemText: '_langsname',
      },
    },
  },
  adultDishId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Dish.getBuffet,
        itemText: '_langsname',
      },
    },
  },
  consumeTypeId: {
    displayName: 'ConsumeTypeSetting',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeType.getList,
      },
    },
  },
}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Dishes.php?op=addJBuffetChild', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('Dishes.php?op=editJBuffetChild', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Dishes.php?op=deleteJBuffetChild', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Dishes.php?op=showJBuffetChild', {
      ...filter,
    })).content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
